@import "config/index";

@import "utilities/index";
@import "base/index";

@import "global";
@import "components/index";
@import "code/index";
@import "elements/index";
@import "bilingual.css";
@import "impressum";
@import "disclaimer";

@media only screen and (min-width: $desktop) {
    #main {
        margin-left: 244px;
        margin-right: 0;
    }
}

hr {
	margin: 48px 0 0 0;
	padding-bottom: 16px;
}

h2 {
    margin-top:48px;
}

h3 {
    margin-bottom: 0;
    margin-top:20px;
}

h3 + p {
    margin-top: 0;
}

#main .content > p:first-child {
    font-size: 1.125rem;
    line-height: 1.7;
	padding-bottom:36px;
}

@media only screen and (min-width: $desktop) {
    #main .content > p:first-child {
        margin-right:360px;
        padding-right:24px;
    }
}

.downloadHolder {
    border-top: 1px solid #e0e0e0;
    margin-top: 48px;
    padding-top: 36px;
    display:none;
}

.downloadHolder .img {
    width:50px;
    height:50px;
	background-image: url(../images/download_hover.svg);
	background-size: cover;
    float:left;
}

.downloadHolder:hover .img {
    background-position: -50px 0;
}

.downloadHolder .text {
	padding-left: 60px;
	padding-top: 8px;
}

html[lang='en-us'] .downloadHolder {
    display:block;
}

html.highContrast .downloadHolder .img {
    display: none;    
}

html.highContrast .downloadHolder .text {    
    padding-left:0;
}

@media only screen and (min-width: $desktop) {
    .downloadHolder {
        border-top:0;
        border-left: 1px solid #e0e0e0;
        position: absolute;
        top: 40px;
        right: 40px;
        padding-top:0;
        padding-left:8px;
        width:310px;
    }

    .downloadHolder .img {
        float:none;
    }

    .downloadHolder .text {
        padding-left: 0;
    }

    html.highContrast .downloadHolder .text {    
        padding-bottom:12px;
    }
}

.panelContent {
	display: -webkit-box;
	display: -moz-box;
	display: -ms-flexbox;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap: wrap;
	-ms-flex-wrap: wrap;
	flex-wrap: wrap;
    list-style-type: none;
    margin-top:24px;
    margin-left:-12px;
}

.panelContent > li {    
    list-style-type: none;
    -webkit-flex: 0 1 100%;
    -ms-flex: 0 1 100%;
    flex: 0 1 100%;
}

.cardsW + h2 {
    margin-top:36px;
}

.cardsW a:hover {
    text-decoration:underline;
}

@media only screen and (min-width: $tablet) {
    .panelContent > li {    
        -webkit-flex: 0 1 50%;
        -ms-flex: 0 1 50%;
        flex: 0 1 50%;
    }
}

@media only screen and (min-width: $desktop) {
    .panelContent > li {    
        -webkit-flex: 0 1 33%;
        -ms-flex: 0 1 33%;
        flex: 0 1 33%;
    }
}

@import "components/hubCards";

/* from conceptual.css */

.toc {
    margin-top: 24px;
}

table {
    border-collapse: collapse;
    border-spacing: 0px;
}

table {
    table-layout: auto;
    width: 100%;
    max-width: 100%;
    font-size: 0.9em;
}

table > thead > tr > th, table > thead > tr > td, table > tbody > tr > th, table > tbody > tr > td, table > tfoot > tr > th, table > tfoot > tr > td {
    padding: 1em;
    line-height: 1.4em;
    vertical-align: top;
    border-width: 1px 0 0 0;
    border-style: solid;
    border-color: #e3e3e3;
}

table > thead > tr > th {
    padding: 1.3em;
    vertical-align: bottom;
    border-width: 0 0 1px 0;
    border-style: solid;
    border-color: #e3e3e3;
}

table > caption + thead > tr:first-child > th, table > caption + thead > tr:first-child > td, table > colgroup + thead > tr:first-child > th, table > colgroup + thead > tr:first-child > td, table > thead:first-child > tr:first-child > th, table > thead:first-child > tr:first-child > td {
    border-top: 0;
}
